import {IResourceComponentsProps, useGetIdentity} from "@pankod/refine-core";
import {Breadcrumb} from "antd";
import {Link} from "react-router-dom";
import {Apps} from "../components/apps";
import {ControlLayout} from "../control_layout";
import {useEffect} from "react";
import urlAppendString from "services/url_append_string";
import { ControlPermitted } from "../components/permissions/control_permission";

const Applications: React.FC<IResourceComponentsProps> = () => {
  return (
    <>
      <Breadcrumb>
        <Breadcrumb.Item>
          <Link to={"/control/analytics"}>Home</Link>
        </Breadcrumb.Item>
        <Breadcrumb.Item>Applications</Breadcrumb.Item>
      </Breadcrumb>
      <Apps />
    </>
  );
};

export const ControlApplicationsPage = () => {
  const {data: user} = useGetIdentity();

  const checkIfUserIsAdmin = () => {
    if (user?.isStaff === false || user?.isAdmin === false) {
      window.location.pathname = "/home";
    }
  };
  useEffect(() => {
    checkIfUserIsAdmin();
    window.history.pushState({}, "", urlAppendString());
  }, []);
  return (
    <ControlLayout>
      <ControlPermitted>
        <Applications />
      </ControlPermitted>
    </ControlLayout>
  );
};
