import {UploadOutlined} from "@ant-design/icons";
import {
  Button,
  Checkbox,
  Form,
  Input,
  Tooltip,
  Upload,
} from "@pankod/refine-antd";
import {useEffect, useState} from "react";

const {TextArea} = Input;

interface Props {
  form?: any;
  onFinish?: any;
  app?: any;
  randKey?: any;
}

export const EditApplicationFormComponent: React.FC<Props> = (
  props: Props
) => {
  const [selectedIcon, setSelectedIcon] = useState<any>();
  const [iconSelected, setIconSelected] = useState(true);

  useEffect(() => {
    props.form.setFieldsValue({
      name: props.app.name,
      url: props.app.url,
      description: props.app.description,
      must_login: props.app.mustLogin,
      is_public: props.app.isPublic,
      is_custom: props.app.isCustom,
      is_active: props.app.isActive,
      icon: props.app.icon,
    });
    setSelectedIcon(null);
  }, [props.randKey]);

  return (
    <>
      <Form
        layout="vertical"
        form={props.form}
        onFinish={(values) => {
          values["id"] = parseInt(props.app.id);
          values["icon"] = selectedIcon;
          props.onFinish(values);
        }}
        requiredMark={false}
        initialValues={{
          remember: false,
          email: "",
          password: "",
        }}
      >
        <Form.Item
          name="name"
          label="Name"
          rules={[
            {required: true, type: "string", message: "Please input name"},
          ]}
        >
          <Input size="large" placeholder="Enter Name ..." />
        </Form.Item>
        <Form.Item
          name="url"
          label="Path"
          rules={[
            {required: true, type: "string", message: "Please input url"},
          ]}
        >
          <Input size="large" placeholder="Enter Path..." />
        </Form.Item>


        <Form.Item
          name="must_login"
          valuePropName="checked"
          rules={[{required: false, type: "boolean"}]}
        >
          <Checkbox>Must Login</Checkbox>
        </Form.Item>

        <Form.Item
          name="is_public"
          valuePropName="checked"
          rules={[{required: false, type: "boolean"}]}
        >
          <Checkbox>Public Available</Checkbox>
        </Form.Item>


        <Form.Item
          name="is_custom"
          valuePropName="checked"
          rules={[{required: false, type: "boolean"}]}
        >
          <Checkbox>Custom Application</Checkbox>
        </Form.Item>

        <Form.Item
          name="is_active"
          valuePropName="checked"
          rules={[{required: false, type: "boolean"}]}
        >
          <Checkbox>is Active</Checkbox>
        </Form.Item>

        <Form.Item
          name="description"
          label="Description"
          style={{marginBottom: "12px"}}
        >
          <TextArea rows={4} />
        </Form.Item>

        <Form.Item name="icon" label={"Icon:"} style={{marginBottom: "12px"}}>
          <Tooltip title={props.app.icon}>
            <span style={{color: "green", display: "block"}}>
              {props.app.icon}
            </span>
          </Tooltip>
          <Upload
            beforeUpload={() => {
              return false;
            }}
            maxCount={1}
            onChange={(file) => {
              var f: any = file.file;
              if (selectedIcon) {
                if (selectedIcon["uid"] !== f.uid) {
                  setSelectedIcon(f);
                  setIconSelected(true);
                }
              } else {
                setSelectedIcon(f);
                setIconSelected(true);
              }
            }}
            onRemove={() => {
              setSelectedIcon(undefined);
              setIconSelected(false);
            }}
            accept="image/*"
          >
            <Button icon={<UploadOutlined />}>Click to Select</Button>
          </Upload>
        </Form.Item>

        <div style={{display: "flex", justifyContent: "flex-end"}}>
          <Button type="primary" size="large" htmlType="submit">
            Save
          </Button>
        </div>
      </Form>
    </>
  );
};
