import { UploadOutlined } from "@ant-design/icons";
import {
  Button,
  Checkbox,
  Form,
  Input,
  Upload,
} from "@pankod/refine-antd";
import { useState } from "react";

const { TextArea } = Input;

interface Props {
  form?: any;
  onFinish?: any;
}

export const CreateApplicationFormComponent: React.FC<
  Props
> = (props: Props) => {
  const [selectedIcon, setSelectedIcon] = useState<any>();
  const [iconSelected, setIconSelected] = useState(false);
  const [ isPublic, setIsPublic ] = useState(false);
  const [isCustom, setIsCustom] = useState(false);
  const [ mustLogin, setMustLogin ] = useState(true);
  
  return (
    <>
      <Form
        layout="vertical"
        form={props.form}
        onFinish={(values) => {
          values['is_public'] = isPublic;
          values['is_custom'] = isCustom;
          values['must_login'] = mustLogin;
          values['icon'] = selectedIcon;
          props.onFinish(values);
        }}
        requiredMark={false}
        initialValues={{
          remember: false,
          email: "",
          password: "",
        }}
      >
        <Form.Item
          name="name"
          label="Name"
          rules={[{ required: true, type: "string", message: "Please input name" }]}
        >
          <Input size="large" placeholder="Enter Name ..." />
        </Form.Item>
        <Form.Item
          name="url"
          label="Path"
          rules={[{ required: true, type: "string", message: "Please input url" }]}
        >
          <Input size="large" placeholder="Enter Path..." />
        </Form.Item>

        <Form.Item
          name="must_login"
          rules={[{ required: false, type: "boolean" }]}
        >
          <Checkbox onChange={() => {setMustLogin(!mustLogin);}}
           defaultChecked={mustLogin}>
            Must Login
          </Checkbox>
        </Form.Item>

        <Form.Item
          name="is_public"
          rules={[{ required: false, type: "boolean" }]}
        >
          <Checkbox onChange={() => {setIsPublic(!isPublic)}}>Public Available</Checkbox>
        </Form.Item>


        <Form.Item
          name="is_custom"
          rules={[{ required: false, type: "boolean" }]}
        >
          <Checkbox onChange={() => {setIsCustom(!isCustom)}}>Custom Application</Checkbox>
        </Form.Item>

        <Form.Item
          name="description"
          label="Description"
          style={{ marginBottom: "12px" }}
        >
          <TextArea rows={4} />
        </Form.Item>

        <Form.Item name="icon" label="Icon" style={{ marginBottom: "12px" }}>
          <Upload
            beforeUpload={() => {
              return false;
            }}
            maxCount={1}
            onChange={(file) => {
              var f:any = file.file;
              if(selectedIcon){
                if(selectedIcon['uid'] !== f.uid){
                  setSelectedIcon(f);
                  setIconSelected(true);
                }
              }else{
                setSelectedIcon(f);
                setIconSelected(true);
              }
             
            }}
            onRemove={() => {
              setSelectedIcon(undefined);
              setIconSelected(false);
            }}
            accept="image/*"
          >
            <Button icon={<UploadOutlined />}>Click to Select</Button>
          </Upload>
        </Form.Item>

        <div style={{ display: "flex", justifyContent: "flex-end" }}>
          <Button type="primary" size="large" htmlType="submit" disabled={!iconSelected}>
            Save
          </Button>
        </div>
      </Form>
    </>
  );
};
