/* eslint-disable jsx-a11y/anchor-is-valid */
import {
  AppstoreOutlined,
  DeleteOutlined,
  EditOutlined,
  ExclamationCircleOutlined,
  MoreOutlined,
  PlusOutlined,
} from "@ant-design/icons";
import {useNavigation} from "@pankod/refine-core";
import {
  Dropdown,
  Menu,
  Space,
  Avatar,
  Card,
  Col,
  Row,
  Skeleton,
  message,
  Button,
  Modal,
  Form,
  Grid,
} from "@pankod/refine-antd";
import {gqlDataProvider} from "api";
import {AppData} from "apps/core/interfaces";
import {useEffect, useState} from "react";
import {CreateApplicationFormComponent} from "./forms/create_appliation_form";
import {EditApplicationFormComponent} from "./forms/edit_application_form";

export interface CreateAppForm {}
export interface EditAppForm {
  id: number;
  name: string;
  description: string;
  url: string;
  icon: string;
  is_public: boolean;
  is_custom: boolean;
  must_login: boolean;
}

interface Props {
  randKey?: any;
}

export const Apps: React.FC<Props> = (props: Props) => {
  const [apps, setApps] = useState<AppData[]>([]);
  const [app, setApp] = useState<AppData>();
  const [loading, setLoading] = useState(true);
  const {push} = useNavigation();
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isEditModalVisible, setIsEditModalVisible] = useState(false);
  const [form] = Form.useForm<CreateAppForm>();
  const [editForm] = Form.useForm<EditAppForm>();
  const breakpoint = Grid.useBreakpoint();
  const isMobile = !breakpoint.lg;

  const setEditApp = (app: AppData) => {
    setApp(app);
    setIsEditModalVisible(true);
  };

  const showAppCreateModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  const onFinish = async (values: any) => {
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "post",
      metaData: {
        operation: "createApp",
        variables: {
          name: {value: values.name, type: "String", required: true},
          description: {
            value: values.description,
            type: "String",
            required: false,
          },
          url: {value: values.url, type: "String", required: true},
          icon: {value: values.icon, type: "Upload", required: true},
          isPublic: {
            value: values.is_public,
            type: "Boolean",
            required: false,
          },
          isCustom: {
            value: values.is_custom,
            type: "Boolean",
            required: false,
          },
          mustLogin: {
            value: values.must_login,
            type: "Boolean",
            required: false,
          },
        },
        fields: [
          "success",
          "message",
          {
            app: [
              "id",
              "name",
              "url",
              "icon",
              "description",
              "created",
              "updated",
              "isActive",
              "canDelete",
              "canEdit",
              "canManage",
              "canAccess",
            ],
          },
        ],
      },
    });

    if (data.success) {
      message.success(data.message);
      setApps([data.app, ...apps]);
      form.resetFields();
      setIsModalVisible(false);
    } else {
      message.error(data.message);
    }
  };

  const onEditFinish = async (values: any) => {
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "post",
      metaData: {
        operation: "updateApp",
        variables: {
          id: {
            value: values.id,
            type: "Int",
            required: true,
          },
          name: {value: values.name, type: "String", required: true},
          description: {
            value: values.description,
            type: "String",
            required: false,
          },
          url: {value: values.url, type: "String", required: true},
          icon: {value: values.icon, type: "Upload", required: false},
          isPublic: {
            value: values.is_public,
            type: "Boolean",
            required: false,
          },
          isCustom: {
            value: values.is_custom,
            type: "Boolean",
            required: false,
          },
          mustLogin: {
            value: values.must_login,
            type: "Boolean",
            required: false,
          },
          isActive: {
            value: values.is_active,
            type: "Boolean",
            required: false,
          },
        },
        fields: [
          "success",
          "message",
          {
            app: [
              "id",
              "name",
              "url",
              "icon",
              "description",
              "created",
              "updated",
              "isActive",
              "canDelete",
              "canEdit",
              "canManage",
              "canAccess",
            ],
          },
        ],
      },
    });

    if (data.success) {
      message.success(data.message);

      setApps(
        apps.map((app) => {
          if (app.id.toString() === values.id.toString()) {
            return data.app;
          }
          return app;
        })
      );
      editForm.resetFields();
      setIsEditModalVisible(false);
    } else {
      message.error(data.message);
    }
  };

  const syncDefaultApps = async () => {
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "post",
      metaData: {
        operation: "syncDefaultApps",
        variables: {},
        fields: ["success", "message"],
      },
    });

    if (data.success) {
      message.success(data.message);
      getApps();
    } else {
      message.error(data.message);
    }
  };

  const appDelete = async (id: number) => {
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "post",
      metaData: {
        operation: "deleteApp",
        variables: {
          id: {value: id, type: "Int", required: true},
        },
        fields: ["success", "message"],
      },
    });

    if (data.success) {
      message.success(data.message);
      setApps(apps.filter((app) => app.id.toString() !== id.toString()));
    } else {
      message.error(data.message);
    }
  };

  const handleAppDelete = (id: number) => {
    Modal.confirm({
      title: "Deleting Application",
      icon: <ExclamationCircleOutlined />,
      content: "Are you sure ...",
      okText: "Delete",
      cancelText: "Cancel",
      onOk: () => appDelete(parseInt(id.toString())),
    });
  };

  const getApps = async () => {
    const {data} = await gqlDataProvider.custom!({
      url: "",
      method: "get",
      metaData: {
        operation: "apps",
        variables: {
          control: {value: true, type: "Boolean", required: true},
        },
        fields: [
          "pages",
          "total",
          "page",
          "hasNext",
          "hasPrev",
          {
            results: [
              "id",
              "name",
              "icon",
              "url",
              "isActive",
              "mustLogin",
              "isCustom",
              "isPublic",
              "created",
              "updated",
              "isActive",
              "canEdit",
              "canDelete",
              "canManage",
              "canAccess",
            ],
          },
        ],
      },
    });
    setApps(data.results);
    setLoading(false);
  };

  useEffect(() => {
    getApps();
  }, []);

  if (loading) {
    return (
      <>
        <Skeleton loading={loading} active></Skeleton>
      </>
    );
  }

  return (
    <>
      <Row>
        <Col span={20}></Col>
        <Col
          span={4}
          style={{
            display: "flex",
            justifyContent: "flex-end",
            marginBottom: 5,
            marginTop: 5,
          }}
        >
          <Button
            size="large"
            onClick={() => {
              form.resetFields();
              showAppCreateModal();
            }}
            danger
          >
            <PlusOutlined />
            Create App
          </Button>

          <Button
            size="large"
            onClick={() => {
              syncDefaultApps();
            }}
            danger
          >
            <PlusOutlined />
            Sync Default Apps
          </Button>
        </Col>
      </Row>
      <Row align="top">
        {apps.length === 0 && (
          <Col span={24} style={{display: "flex", justifyContent: "center"}}>
            <h3>No Apps Found ...</h3>
          </Col>
        )}
        {apps.map((app) => {
          return (
            <Col span={isMobile ? 12 : 4}>
              <Card
                hoverable={app.isActive}
                style={{marginRight: 10, marginBottom: 10}}
              >
                <Space style={{right: 0, position: "absolute"}}>
                  <Dropdown
                    trigger={["click"]}
                    overlay={
                      <Menu>
                        <Menu.Item
                          icon={<EditOutlined />}
                          onClick={() => {
                            setEditApp(app);
                          }}
                          disabled={!app?.canDelete}
                        >
                          <a target="_blank" rel="noopener noreferrer">
                            Edit
                          </a>
                        </Menu.Item>
                        <Menu.Item
                          icon={<DeleteOutlined />}
                          onClick={() => {
                            handleAppDelete(app.id);
                          }}
                          disabled={!app?.canDelete}
                        >
                          <a target="_blank" rel="noopener noreferrer">
                            Delete {app.canDelete}
                          </a>
                        </Menu.Item>
                      </Menu>
                    }
                  >
                    <a
                      className="ant-dropdown-link"
                      onClick={(e) => e.preventDefault()}
                    >
                      <MoreOutlined
                        style={{
                          color: "orange",
                          fontSize: 28,
                          fontWeight: "bold",
                        }}
                      />
                    </a>
                  </Dropdown>
                </Space>
                <Space
                  direction="vertical"
                  align="center"
                  style={{width: "100%", justifyContent: "center"}}
                  onClick={() => {}}
                >
                  <Avatar
                    size={{xs: 24, sm: 32, md: 40, lg: 40, xl: 40, xxl: 80}}
                    icon={<AppstoreOutlined />}
                    src={app.icon}
                  />
                  {app.name}
                </Space>
              </Card>
            </Col>
          );
        })}
      </Row>

      <Modal
        title="Creating An Application"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        footer={[]}
      >
        <CreateApplicationFormComponent form={form} onFinish={onFinish} />
      </Modal>

      <Modal
        title="Edit Application"
        visible={isEditModalVisible}
        width={isMobile ? "100vw" : "30vw"}
        onOk={() => {
          setIsEditModalVisible(false);
        }}
        onCancel={() => {
          setIsEditModalVisible(false);
        }}
        footer={[]}
      >
        <EditApplicationFormComponent
          app={app}
          form={editForm}
          onFinish={onEditFinish}
          randKey={Math.random()}
        />
      </Modal>
    </>
  );
};
